import { Component, OnInit, Inject, ElementRef, ViewChild } from '@angular/core';
import { FormGroup, FormBuilder, Validators, ValidatorFn, AbstractControl } from '@angular/forms';
import { MatSnackBar } from '@angular/material/snack-bar';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { MtxDialogData } from '@ng-matero/extensions';
import { Router } from '@angular/router';
import { Observable } from 'rxjs';
import { map, startWith, tap } from 'rxjs/operators';
import { MessageConstant } from 'app/shared/utils/message.constant';
import { ViewEncapsulation } from '@angular/core';
import { fuseAnimations } from '@fuse/animations';
import { MenuMasterService } from 'app/shared/services/menu-master/menu-master.service';

function autocompleteObjectValidator(): ValidatorFn {
  return (control: AbstractControl): { [key: string]: any } | null => {
    if (typeof control.value === 'string') {
      return { 'invalidAutocompleteObject': { value: control.value } }
    }
    return null  /* valid option selected */
  }
}

@Component({
  selector: 'app-view-dialog',
  templateUrl: './view-dialog.component.html',
  styleUrls: ['./view-dialog.component.scss'],
  encapsulation: ViewEncapsulation.None,
  animations: fuseAnimations
})
export class ViewDialogComponent implements OnInit {

  params: any;
  isEditMode = false;
  form: FormGroup;
  localStorage: any;
  nav_level:any;
  project:any;
  menuType:any;
  menuIcon:any;
  parentId:any;
  display_order:any;
  filteredOptionsNavLevel : Observable<any[]>;
  filteredOptionsProject : Observable<any[]>;
  filteredOptionsMenuType : Observable<any[]>;
  filteredOptionsMenuIcon : Observable<any[]>;
  filteredOptionsParentId : Observable<any[]>;
  status = false;

  @ViewChild("activeElement") active: any;

  // messageConstant = MessageConstant;
  constructor(private fb: FormBuilder, private snackBar: MatSnackBar, public dialogRef: MatDialogRef<ViewDialogComponent>,
    @Inject(MAT_DIALOG_DATA) public data: MtxDialogData, private router: Router,private menuMasterService:MenuMasterService) {

  }

  ngOnInit() {
    this.initialize();
    
  }

  initialize() {
    this.localStorage = JSON.parse(localStorage.getItem('userDetails'));
    this.params = this.data;
    this.isEditMode = this.params.action == 'CREATE' ? false : true;

    this.form = this.fb.group({
      nav_level: ['', [autocompleteObjectValidator(), Validators.required]],
      project: ['', [autocompleteObjectValidator(), Validators.required]],
      parent_id: ['', [autocompleteObjectValidator(), Validators.required]],
      nav_id: ['', [Validators.required]],
      // code: ['', [ Validators.pattern(/^[a-zA-Z0-9]*$/)]],
      title: ['', [Validators.required, Validators.pattern(/^[a-zA-Z0-9 ]*$/)]],
      //description: ['',[Validators.pattern(/^[a-zA-Z0-9 ]*$/)]],
      type: ['', [autocompleteObjectValidator(), Validators.required]],
      icon: ['', [autocompleteObjectValidator(), Validators.required]],
      icon_url: [''],
      url: ['', [Validators.required]],
      display_ord: ['', Validators.required],
      status: this.status == true ? 'Y' : 'N'
    });

    if (this.isEditMode) {
      this.assignValues(this.params);
    }
    this.getNavLevel();
    this.getParentByNavLevel(this.params.data.nav_level);
    this.getProject();
    this.getType();
    this.getMenuIcon();

  }

  close() {
    this.dialogRef.close();
  }
  closeActionPanel() {
    this.dialogRef.close();
  }

  getErrorMessage(form: FormGroup) {
    return form.get('email').hasError('required')
      ? 'You must enter a value'
      : form.get('email').hasError('email')
        ? 'Not a valid email'
        : '';
  }

  assignValues(params) {
     this.form.get('nav_id').setValue(params.data.nav_id);
     this.form.get('title').setValue(this.params.data.title);
     this.form.get('icon_url').setValue(params.data.icon_url);
     this.form.get('url').setValue(params.data.url);
     this.form.get('display_ord').setValue(params.data.display_order);
     this.status = this.params.data.status == 'Y' ? true : false; 
  }

 getNavLevel(){
      this.menuMasterService.getNavLevel().subscribe(
        (resp: any) => {
          if (resp.status == true) {
            this.nav_level =resp.data.nav_level;
            //this.display_order = resp.data.next_display_order;
            if(this.isEditMode){
                const toSelect = this.nav_level.find(c => c == this.params.data.nav_level);
                this.form.get('nav_level').setValue(toSelect);
                this.filteredOptionsNavLevel = this.form.controls.nav_level.valueChanges
              .pipe(
                startWith(''),
                map(value => typeof value === 'string' ? value : value),
                map(name => name ? this._filterNavLevel(name) : this.nav_level)
              );
            }

          }
        }, (err) => { }
      );

    }
  onNavLevelChange($navLevel_id){
    //console.log($navLevel_id);
    this.form.get('parent_id').setValue('');
    this.getParentByNavLevel($navLevel_id);
    //this.form.get('display_ord').setValue( this.display_order);
  }
  private _filterNavLevel(name: string): any[] {
    const filterValue = name.toLowerCase();
    return this.nav_level.filter(option => option.toString().toLowerCase().includes(filterValue));
  }
  displayNavLevel(item: any): string{
    return item && item ? item : '';
  }

  getProject() {
    this.menuMasterService.getProject().subscribe(
      (resp: any) => {
        if (resp != null) {
          this.project = resp;
          if(this.isEditMode){
            const toSelect = this.project.find(c => c.id == this.params.data.project_id);
           this.form.get('project').setValue(toSelect);
           this.filteredOptionsProject = this.form.controls.project.valueChanges
            .pipe(
              startWith(''),
              map(value => typeof value === 'string' ? value : value.code),
              map(name => name ? this._filterProject(name) : this.project)
            );
          }

        }
      }, (err) => { }

    );
  }
  private _filterProject(name: string): any[] {
    const filterValue = name.toLowerCase();
    return this.project.filter(option => option.code.toLowerCase().includes(filterValue));
  }
  displayProject(item: any): string{
    return item && item.code ? item.code : '';
  }

  getType() {
    this.menuMasterService.getMenuType().subscribe(
      (resp: any) => {
        if (resp.status == true) {
          this.menuType = resp.data;
          if(this.isEditMode){
            const toSelect = this.menuType.find(c => c.type == this.params.data.type);
           this.form.get('type').setValue(toSelect);
           this.filteredOptionsMenuType = this.form.controls.type.valueChanges
            .pipe(
              startWith(''),
              map(value => typeof value === 'string' ? value : value.type),
              map(name => name ? this._filterMenuType(name) : this.menuType)
            );

          }
          
        }
      }, (err) => { }

    );
  }
  private _filterMenuType(name: string): any[] {
    const filterValue = name.toLowerCase();
    return this.menuType.filter(option => option.type.toLowerCase().includes(filterValue));
  }
  displayType(item: any): string{
    return item && item.type ? item.type : '';
  }

  getMenuIcon() {
    this.menuMasterService.getMenuIcon().subscribe(
      (resp: any) => {
        if (resp.status == true) {
          this.menuIcon = resp.data;
          if(this.isEditMode){
            const toSelect = this.menuIcon.find(c => c.icon == this.params.data.icon);
           this.form.get('icon').setValue(toSelect);
           this.filteredOptionsMenuIcon = this.form.controls.icon.valueChanges
            .pipe(
              startWith(''),
              map(value => typeof value === 'string' ? value : value.icon),
              map(name => name ? this._filterMenuIcon(name) : this.menuIcon)
            );
          }
          
        }
      }, (err) => { }

    );
  }
  private _filterMenuIcon(name: string): any[] {
    const filterValue = name.toLowerCase();
    return this.menuIcon.filter(option => option.icon == null ? false :  option.icon.toLowerCase().includes(filterValue));
  }
  displayIcon(item: any): string{
    return item && item.icon ? item.icon : '';
  }

  getParentByNavLevel(navLevel_id) {
    this.menuMasterService.getParentByNavLevel(navLevel_id).subscribe(
      (resp: any) => {
        if (resp.status == true) {
          this.parentId = resp.data;
          if(this.isEditMode){
            const toSelect = this.parentId.find(c => c.id == this.params.data.parent_id);
           this.form.get('parent_id').setValue(toSelect);
           this.filteredOptionsParentId = this.form.controls.parent_id.valueChanges
            .pipe(
              startWith(''),
              map(value => typeof value === 'string' ? value : (value == null ? '' : value.title)),
              map(name => name ? this._filterParentId(name) : this.parentId)
            );
          }

        }
      }, (err) => { }

    );

  }
  private _filterParentId(name: string): any[] {
    const filterValue = name.toLowerCase();
    return this.parentId.filter(option => option.title.toLowerCase().includes(filterValue));
  }
  displayParentId(item: any): string{
    return item && item.title ? item.title : '';
  }

  updateStatus(event) {
    this.status = event.checked;
    this.active.checked = this.status;
  }



}










