
import { NgModule} from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { TaxSettingCrudComponent } from './tax-setting-crud/tax-setting-crud.component';
import { TABS_DEMO_ROUTES } from './tax-setting-crud/routes';
import { AuthGuard } from 'app/shared/interceptors/auth-gaurd';
const routes : Routes = [
    {
        path : 'crud',component : TaxSettingCrudComponent,canActivate: [AuthGuard], children : TABS_DEMO_ROUTES
    }
]

@NgModule({
    imports : [RouterModule.forChild(routes)],
    exports : [RouterModule]
})

export class TaxSettingRoutingModule{}